/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable import/no-extraneous-dependencies */
import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { map, sortBy, each, includes, capitalize, isEqual } from 'lodash';
import {
  Button,
  withStyles,
  TextField,
  CircularProgress,
  Checkbox,
  Grid,
  FormControlLabel,
  Paper,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  ListItemText,
  OutlinedInput,
  Chip,
} from '../../materialLib';
import { COLORS, ITEM_HEIGHT, ITEM_PADDING_TOP } from '../../constants';
import Link from '../Link';

const StyledTypography = withStyles({
  root: {
    marginBottom: 10,
  },
})(Typography);

const StyledPaper = withStyles({
  root: {
    padding: 15,
    marginBottom: 20,
    width: '100%',
    boxShadow: 'none',
  },
})(Paper);

const StyledFormControlLabel = withStyles({
  root: {
    display: '-webkit-box',
  },
})(FormControlLabel);

const StyledCheckbox = withStyles({
  root: {
    color: `${COLORS.NAVY_BLUE} !important`,
    display: 'inline-block',
    padding: `0 12px`,
  },
})(Checkbox);

const GridWrapper = withStyles({
  container: {
    padding: 10,
  },
})(Grid);

const StyledGrid = withStyles({
  item: {
    padding: `0  !important`,
    margin: `0 0 20px`,
  },
})(Grid);

const styles = theme => ({
  submit: {
    backgroundColor: COLORS.NAVY_BLUE,
    margin: `0 ${theme.spacing.unit}px ${theme.spacing.unit}px 0 `,
    textTransform: 'capitalize',
    float: 'right',
  },
  btnWrapper: {
    margin: `${theme.spacing.unit * 3}px auto`,
  },
  progress: {
    margin: 0,
    color: 'white',
  },

  root: {
    display: 'block',
  },
  colorSwitchBase: {
    color: COLORS.NAVY_BLUE,
    '&$colorChecked': {
      color: COLORS.NAVY_BLUE,
      '& + $colorBar': {
        backgroundColor: COLORS.CURIOS_BLUE,
      },
    },
  },
  colorBar: {
    color: COLORS.NAVY_BLUE,
  },
  colorChecked: {
    color: COLORS.NAVY_BLUE,
  },

  formLabel: {
    color: COLORS.NAVY_BLUE,
    margin: 0,
    fontWeight: 'bold',
  },
  checkboxWrapper: {},
  formControl: {
    minWidth: 120,
    width: '100%',
    marginTop: 10,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: theme.spacing.unit / 4,
  },
});

const UserProfile = props => {
  let InputLabelRef = null;

  const {
    classes,
    formData,
    error,
    handleChange,
    handleSubmit,
    type,
    createUser,
    onCancel,
    loading,
    handleCheck,
    handleOpen,
    resetButtonLabel,
    handleSelect,
    rolesList,
    style,
    operationType,
  } = props;

  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: style,
      },
    },
  };
  const {
    username,
    firstName,
    password,
    lastName,
    permissions,
    roles,
  } = formData;
  const [labelWidth, setLabelWidth] = useState(0);
  useEffect(
    () =>
      setLabelWidth(
        labelWidth => ReactDOM.findDOMNode(InputLabelRef).offsetWidth,
      ),
    [],
  );

  const formatData = key => {
    let items = [];
    each(permissions, permission => {
      const permissionObj = permission;
      if (includes(permission.name, 'READ') && permission.name !== 'READ') {
        permissionObj.action = 'READ';
      } else if (includes(permission.name, 'CREATE')) {
        permissionObj.action = 'CREATE';
      } else if (includes(permission.name, 'MODIFY')) {
        permissionObj.action = 'MODIFY';
      } else if (includes(permission.name, 'REMOVE')) {
        permissionObj.action = 'REMOVE';
      } else if (includes(permission.name, 'DELETE')) {
        permissionObj.action = 'DELETE';
      } else {
        permissionObj.action = 'WRITE';
      }
      if (includes(permission.name, key)) {
        items = [...items, permissionObj];
      }
    });
    return sortBy(items, ['name']);
  };

  return (
    <form>
      <StyledPaper>
        <StyledTypography variant="h5" component="h3">
          Basic details
        </StyledTypography>
        <TextField
          id="firstName"
          label="First Name"
          className={classes.textField}
          value={capitalize(firstName)}
          onChange={handleChange}
          margin="normal"
          variant="outlined"
          style={{ borderColor: error.firstName ? 'red' : '' }}
          fullWidth
          required
        />
        <TextField
          id="lastName"
          label="Last Name"
          className={classes.textField}
          value={capitalize(lastName)}
          onChange={handleChange}
          margin="normal"
          variant="outlined"
          style={{ borderColor: error.lastName ? 'red' : '' }}
          fullWidth
          required
        />
        <TextField
          id="username"
          label="User Name"
          className={classes.textField}
          value={username}
          onChange={handleChange}
          margin="normal"
          variant="outlined"
          style={{ borderColor: error.username ? 'red' : '' }}
          fullWidth
          required
          disabled={!createUser}
        />
        {createUser && (
          <TextField
            id="password"
            label="Password"
            className={classes.textField}
            value={password}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            style={{ borderColor: error.password ? 'red' : '' }}
            type="password"
            fullWidth
            required
          />
        )}
        <FormControl
          required
          variant="outlined"
          className={classes.formControl}
        >
          <InputLabel
            ref={ref => {
              InputLabelRef = ref;
            }}
            htmlFor="outlined-age-native-simple"
          >
            Roles
          </InputLabel>
          <Select
            multiple
            value={roles}
            onChange={handleSelect}
            input={
              <OutlinedInput
                labelWidth={labelWidth}
                name="Users"
                id="outlined-age-native-simple"
              />
            }
            renderValue={selected => (
              <div className={classes.chips}>
                {selected.map(value => (
                  <Chip key={value} label={value} className={classes.chip} />
                ))}
              </div>
            )}
            MenuProps={MenuProps}
          >
            {rolesList.map(user => (
              <MenuItem key={user} value={user}>
                {user}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </StyledPaper>
      <StyledPaper>
        <StyledTypography variant="h5" component="h3">
          Permissions
        </StyledTypography>
        <GridWrapper container spacing={24}>
          <StyledGrid item xs={6} sm={3}>
            <p className={classes.formLabel}>CONTAINER</p>
            {map(formatData('CONTAINER'), permission => (
              <StyledFormControlLabel
                control={
                  <StyledCheckbox
                    checked={permission.checked}
                    value={permission.name}
                    onChange={handleCheck(permission.name)}
                  />
                }
                label={permission.action}
                key={permission.name}
              />
            ))}
          </StyledGrid>
          <StyledGrid item xs={6} sm={3}>
            <p className={classes.formLabel}>REPOSITORY</p>
            {map(formatData('REPOSITORY'), permission => (
              <StyledFormControlLabel
                control={
                  <StyledCheckbox
                    checked={permission.checked}
                    value={permission.name}
                    onChange={handleCheck(permission.name)}
                  />
                }
                label={permission.action}
                key={permission.name}
              />
            ))}
          </StyledGrid>
          <StyledGrid item xs={6} sm={3}>
            <p className={classes.formLabel}>TYPE</p>
            {map(formatData('TYPE'), permission => (
              <StyledFormControlLabel
                control={
                  <StyledCheckbox
                    checked={permission.checked}
                    value={permission.name}
                    onChange={handleCheck(permission.name)}
                  />
                }
                label={permission.action}
                key={permission.name}
              />
            ))}
          </StyledGrid>
          <StyledGrid item xs={6} sm={3}>
            <p className={classes.formLabel}>USER</p>
            {map(formatData('USER'), permission => (
              <StyledFormControlLabel
                control={
                  <StyledCheckbox
                    checked={permission.checked}
                    value={permission.name}
                    onChange={handleCheck(permission.name)}
                  />
                }
                label={permission.action}
                key={permission.name}
              />
            ))}
          </StyledGrid>
          <StyledGrid item xs={6} sm={3}>
            <p className={classes.formLabel}>ARTIFACT</p>
            {map(formatData('ARTIFACT'), permission => (
              <StyledFormControlLabel
                control={
                  <StyledCheckbox
                    checked={permission.checked}
                    value={permission.name}
                    onChange={handleCheck(permission.name)}
                  />
                }
                label={permission.action}
                key={permission.name}
              />
            ))}
          </StyledGrid>
        </GridWrapper>
      </StyledPaper>
      <div className={classes.btnWrapper}>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={onCancel}
        >
          Cancel
        </Button>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit}
        >
          {loading ? (
            <CircularProgress size={22} className={classes.progress} />
          ) : (
              type
            )}
        </Button>
        {isEqual(operationType, 'changePassword') && (
          <Link
            style={{
              textDecoration: 'none',
              color: COLORS.DENIM,
              float: 'left',
            }}
            onClick={handleOpen}
            to="#"
          >
            {resetButtonLabel}
          </Link>
        )}
      </div>
    </form>
  );
};

UserProfile.propTypes = {
  classes: PropTypes.object.isRequired,
  formData: PropTypes.object.isRequired,
  error: PropTypes.object.isRequired,
  handleChange: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  type: PropTypes.string.isRequired,
  createUser: PropTypes.bool,
  onCancel: PropTypes.func.isRequired,
  loading: PropTypes.bool.isRequired,
  handleCheck: PropTypes.func.isRequired,
  handleOpen: PropTypes.func,
  resetButtonLabel: PropTypes.string,
  handleSelect: PropTypes.func,
  rolesList: PropTypes.array,
  theme: PropTypes.object,
  operationType: PropTypes.string,
};

UserProfile.defaultProps = {
  createUser: false,
  handleOpen: () => { },
  resetButtonLabel: null,
  handleSelect: () => { },
  theme: {},
  rolesList: [],
  operationType: null,
};

export default withStyles(styles, { theme: true })(UserProfile);
