/* eslint-disable no-shadow */
/* eslint-disable no-plusplus */
/* eslint-disable no-await-in-loop */
/* eslint-disable no-nested-ternary */
/* eslint-disable no-param-reassign */
/* eslint-disable prefer-const */
/* eslint-disable linebreak-style */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  filter,
  map,
  extend,
  get,
  find,
  chain,
  omit,
  some,
  each,
  every,
  isEqual,
  pick,
  includes,
  reject,
  isEmpty,
  upperCase,
  sortBy,
} from 'lodash';
import { Typography } from '../../materialLib';
import PageWrapper from '../../components/PageWrapper';
import Users from '../users-list/Users';
import {
  API_URLS,
  MESSAGES,
  BREADCRUMBS,
  USER_TABLE_HEADERS,
  USER_PROFILE_FIELDS,
  DEFAULT_PERMISSIONS,
  RESET_PASSWORD_FIELDS,
} from '../../constants';
import withUserManagement from '../../actions/withUserManagement';
import {
  getGroups,
  getUsers,
  changeTab,
  getPermissions,
} from '../../actions/userManagement';
import { openNotifier } from '../../actions/notifier';
import withNotifier from '../../actions/withNotifier';
import formHelper from '../../lib/formHelper';
import Modal from '../../components/Modal';
import UserProfileModal from '../users-list/components/UserProfileModal';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3, width: '100%' }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

const UserManagement = (props, context) => {
  const [order, setOrder] = useState('asc');
  const [permissionUpdateType, setPermissionUpdateType] = useState(
    'bulkUpdate',
  );
  let [orderBy, setOrderBy] = useState('username');
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(50);
  const [open, setOpen] = useState(false);
  let [selectedUsers, setUsers] = useState([]);
  let [permissions, setPermissions] = useState([]);
  let [selectedUser, setUser] = useState({});
  let [error, setUserFieldError] = useState({
    firstName: '',
    lastName: '',
    password: '',
    username: '',
  });
  const [openEditModal, setOpenEditModal] = useState(false);
  const [openViewModal, setOpenViewModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [actionType, setActionType] = useState('newUser');
  const [selectAll, setSelectAll] = useState(false);
  const { userManagement } = props;
  const newPermissions = permissions;

  useEffect(() => {
    getUserPermissions();
    getUserList();
  }, []);

  const handleRequestSort = (event, property) => {
    orderBy = property;
    let currentOrder = 'desc';

    if (orderBy === property && order === 'desc') {
      currentOrder = 'asc';
    }
    setOrder(() => currentOrder);
    setOrderBy(() => orderBy);
  };

  const handleChangePage = (event, page) => {
    setPage(() => page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(() => event.target.value);
  };
  const getUserList = () => {
    getResponse({
      key: 'selectedUsers',
      apiUrl: API_URLS.users,
      method: setUsers,
    });
  };

  const getUserPermissions = () => {
    getResponse({
      key: 'permissions',
      apiUrl: API_URLS.permissions,
      method: setPermissions,
    });
  };

  const getResponse = async paramObj => {
    await setLoading(() => true);
    try {
      const response = await context.fetch(`${paramObj.apiUrl}`);
      const data = await response.json();
      let { resources } = data;
      if (paramObj.key === 'permissions') {
        resources = map(sortBy(resources, ['name']), resource => {
          let flag = false;
          if (resource.name === 'READ') {
            flag = true;
          }
          return {
            ...resource,
            checked: flag,
            label: upperCase(resource.name),
            numeric: false,
            disablePadding: true,
          };
        });
        await setPermissions(resources);
      } else {
        resources = map(
          filter(resources, user => user.username !== 'super.user'),
          resource => ({
            ...resource,
            name: resource.name.toLowerCase(),
            checked: false,
          }),
        );
        await setUsers(resources);
      }
      await setLoading(() => false);
    } catch (e) {
      await setLoading(() => false);
      console.error(e);
    }
  };

  const handleChangeSearch = async (event, key, method) => {
    const currentValue = event.target.value.toLowerCase();
    if (currentValue) {
      setUsers(
        filter(selectedUsers, item => item.name.indexOf(currentValue) > -1),
      );
    } else {
      getResponse({ key, apiUrl: API_URLS[key], method });
    }
  };

  const handleClickOpen = () => {
    setOpen(() => true);
  };

  const handleClose = () => {
    setOpen(() => false);
    setUser({});
  };

  const handleOpenEditModal = async type => {
    await setOpenEditModal(() => true);
    await setActionType(() => type);
    if (isEqual(type, 'newUser')) {
      selectedUser = { ...selectedUser, permissions: newPermissions };
      setUser(selectedUser);
    } else {
      setUser({
        firstName: 'NA',
        lastName: 'NA',
        permissions,
        username: 'NA',
      });
    }
  };

  const handleCloseEditModal = () => {
    setOpenEditModal(() => false);
    setUser({});
    setPermissionUpdateType(() => 'bulkUpdate');
  };

  const handleOpenViewModal = async data => {
    let { permissions } = data;
    permissions = map(permissions, permission =>
      extend(permission, { checked: true }),
    );
    permissions = [
      ...filter(newPermissions, obj => !find(permissions, { name: obj.name })),
      ...permissions,
    ];
    await setUser({ ...data, permissions });
    await setOpenViewModal(() => true);
  };

  const createPermissionObject = (event, permissions) => {
    permissions = filter(permissions, item => item.name !== event.target.value);
    return [
      ...permissions,
      { name: event.target.value, checked: event.target.checked },
    ];
  };

  const handleCheck = (event, obj) => {
    if (
      selectedUsers.length &&
      includes(['resetPassword', 'existingUser'], actionType)
    ) {
      let { permissions } = obj;
      selectedUsers = [
        ...chain(selectedUsers)
          .reject({ username: obj.username })
          .value(),
        { ...obj, permissions: createPermissionObject(event, permissions) },
      ];
      setUsers(selectedUsers);
    } else {
      let { permissions } = selectedUser;
      setUser({
        ...selectedUser,
        permissions: createPermissionObject(event, permissions),
      });
    }
  };

  const deleteUser = async e => {
    let currentSelectedUsers = filterSelectedUser();
    e.preventDefault();
    try {
      let response = null;
      let data = null;
      await asyncForEach(currentSelectedUsers, async user => {
        response = await context.fetch(
          `${API_URLS.users}/${get(user, 'keys.key')}`,
          {
            method: 'DELETE',
          },
        );
        data = await response.json();
      });
      if (response.status === 200) {
        await props.openNotifier({
          openNotify: true,
          message: MESSAGES.user.delete.success,
          variant: 'success',
        });
      } else {
        await props.openNotifier({
          openNotify: true,
          message: data.message,
          variant: 'error',
        });
      }
      await setOpen(() => false);
      await setUser({});
      await setPermissionUpdateType(() => 'bulkUpdate');
      await getUserList();
    } catch (err) {
      setOpen(() => false);
      setUser({});
    }
  };

  const validator = user => {
    const { firstName, lastName, username, password } = user;
    if (includes(['newUser', 'existingUser'], actionType)) {
      if (some([username, firstName, lastName], formHelper.isInputEmpty)) {
        each(USER_PROFILE_FIELDS, item => {
          error[item] = formHelper.isInputEmpty(user[item])
            ? MESSAGES.mandatoryFields
            : '';
        });
      }
    } else if (some([username, password], formHelper.isInputEmpty)) {
      each(RESET_PASSWORD_FIELDS, item => {
        error[item] = formHelper.isInputEmpty(user[item])
          ? MESSAGES.mandatoryFields
          : '';
      });
    }
  };

  const isValid = () => {
    if (selectedUsers.length) {
      each(selectedUsers, user => {
        validator(user);
      });
    } else {
      validator(selectedUser);
    }
    setUserFieldError(error);

    return every(error, formHelper.isInputEmpty);
  };

  const asyncForEach = async (array, callback) => {
    for (let index = 0; index < array.length; index++) {
      await callback(array[index], index, array);
    }
  };

  const saveUserDetails = async e => {
    let currentSelectedUsers = filterSelectedUser();

    let apiUrl = isEqual(actionType, 'newUser')
      ? `${API_URLS.signUp}`
      : isEqual(actionType, 'resetPassword')
        ? `${API_URLS.resetPassword}`
        : `${API_URLS.users}`;

    let apiMethod = includes(['newUser', 'resetPassword'], actionType)
      ? 'POST'
      : 'PUT';

    if (currentSelectedUsers.length && isEmpty(selectedUser)) {
      if (includes(['newUser', 'existingUser'], actionType)) {
        selectedUsers = map(selectedUsers, user => ({
          ...user,
          permissions: chain(user.permissions)
            .filter({ checked: true })
            .map(item => omit(item, ['checked', 'action']))
            .value(),
        }));
      } else {
        selectedUsers = map(selectedUsers, user =>
          pick({ ...user, newPassword: user.password }, [
            'username',
            'newPassword',
          ]),
        );
      }
    } else if (
      !isEmpty(selectedUser) &&
      isEqual(permissionUpdateType, 'bulkUpdate')
    ) {
      currentSelectedUsers = map(currentSelectedUsers, user => ({
        ...user,
        permissions: chain(selectedUser.permissions)
          .filter({ checked: true })
          .map(item =>
            omit(item, [
              'checked',
              'action',
              'label',
              'disablePadding',
              'numeric',
            ]),
          )
          .value(),
      }));
    } else if (includes(['newUser', 'existingUser'], actionType)) {
      selectedUser = {
        ...selectedUser,
        permissions: chain(get(selectedUser, 'permissions'))
          .filter({ checked: true })
          .map(item => omit(item, ['checked', 'action']))
          .value(),
      };
    } else {
      selectedUser = pick(
        { ...selectedUser, newPassword: selectedUser.password },
        ['username', 'newPassword'],
      );
    }

    e.preventDefault();

    if (!isValid()) {
      await props.openNotifier({
        openNotify: true,
        message: MESSAGES.emptyField,
        variant: 'warning',
      });
      return;
    }

    await setLoading(() => true);

    try {
      let data = null;
      let response = null;
      if (currentSelectedUsers.length && isEmpty(selectedUser)) {
        await asyncForEach(currentSelectedUsers, async user => {
          response = await context.fetch(
            isEqual(actionType, 'resetPassword')
              ? `${apiUrl}`
              : `${apiUrl}/${get(user, 'keys.key')}`,
            {
              method: apiMethod,
              body: JSON.stringify(user),
              headers: {
                'Content-Type': 'application/json',
              },
            },
          );
          data = await response.json();
        });
      } else if (
        !isEmpty(selectedUser) &&
        isEqual(permissionUpdateType, 'bulkUpdate')
      ) {
        await asyncForEach(currentSelectedUsers, async user => {
          response = await context.fetch(`${apiUrl}/${get(user, 'keys.key')}`, {
            method: apiMethod,
            body: JSON.stringify(user),
            headers: {
              'Content-Type': 'application/json',
            },
          });
          data = await response.json();
        });
      } else {
        response = await context.fetch(`${apiUrl}`, {
          method: apiMethod,
          body: JSON.stringify(selectedUser),
          headers: {
            'Content-Type': 'application/json',
          },
        });
        data = await response.json();
      }
      if (response.ok) {
        await props.openNotifier({
          openNotify: true,
          message: MESSAGES.user.update.success,
          variant: 'success',
        });
      } else {
        await props.openNotifier({
          openNotify: true,
          message: data.message,
          variant: 'error',
        });
      }
      await setOpenEditModal(() => false);
      await setUsers([]);
      await setUser({});
      await setPermissionUpdateType(() => 'bulkUpdate');
      await getUserList();
      await setLoading(() => false);
    } catch (er) {
      console.error(er);
    }
  };

  const selectCurrentUser = (event, user) => {
    let currentUser = user;
    if (get(event, 'target.checked')) {
      currentUser = { ...currentUser, checked: true };
    } else {
      currentUser = { ...currentUser, checked: false };
    }
    setUsers([
      ...reject(selectedUsers, { keys: { key: user.keys.key } }),
      currentUser,
    ]);
  };

  const handleChange = async (e, obj) => {
    const { id, value } = e && e.target;
    if (includes(['resetPassword', 'existingUser'], actionType)) {
      obj[id] = value;
      error[id] = formHelper.isInputEmpty(obj[id])
        ? MESSAGES.mandatoryFields
        : '';
      selectedUsers = [
        ...chain(selectedUsers)
          .reject({ username: obj.username })
          .value(),
        obj,
      ];
      await setUsers(selectedUsers);
    } else {
      selectedUser[id] = value;
      error[id] = formHelper.isInputEmpty(selectedUser[id])
        ? MESSAGES.mandatoryFields
        : '';
      await setUser(selectedUser);
    }
    await setUserFieldError(error);
  };

  const selectAllItems = async e => {
    if (get(e, 'target.checked')) {
      await setUsers(
        chain(selectedUsers)
          .map(item => ({ ...item, checked: true }))
          .value(),
      );
      await setSelectAll(() => true);
    } else {
      await setUsers(
        chain(selectedUsers)
          .map(item => ({ ...item, checked: false }))
          .value(),
      );
      await setSelectAll(() => false);
    }
  };

  const switchPermissionUpdateType = event => {
    if (isEqual(event.target.value, 'bulkUpdate')) {
      setUser({
        firstName: 'NA',
        lastName: 'NA',
        permissions,
        username: 'NA',
      });
    } else {
      setUser({});
    }
    setPermissionUpdateType(() => event.target.value);
  };

  const changeBulkPermissionUpdate = (event, obj) => {
    setUser({
      ...obj,
      permissions: createPermissionObject(event, get(obj, 'permissions')),
    });
  };

  const filterSelectedUser = () =>
    chain(selectedUsers)
      .filter({ checked: true })
      .value();

  return (
    <PageWrapper
      breadCrumb={BREADCRUMBS.USER_MANAGEMENT}
      style={{ flexGrow: 1, width: '100%', boxShadow: 'none', margin: 0 }}
    >
      <TabContainer>
        <Users
          users={(userManagement && userManagement.users) || []}
          handleChangeSearch={handleChangeSearch}
          getUserList={getUserList}
          handleClickOpen={handleClickOpen}
          handleClose={handleClose}
          handleSubmit={deleteUser}
          open={open}
          handleRequestSort={handleRequestSort}
          handleChangePage={handleChangePage}
          handleChangeRowsPerPage={handleChangeRowsPerPage}
          rowsPerPage={rowsPerPage}
          page={page}
          headers={[
            ...USER_TABLE_HEADERS,
            ...sortBy(DEFAULT_PERMISSIONS, ['name']),
          ]}
          order={order}
          orderBy={orderBy}
          selectCurrentUser={selectCurrentUser}
          selectedUser={selectedUser}
          selectedUsers={selectedUsers}
          handleOpenEditModal={handleOpenEditModal}
          error={error}
          handleCheck={handleCheck}
          loading={loading}
          selectAllItems={selectAllItems}
          selectAll={selectAll}
          permissions={permissions}
        />
      </TabContainer>
      <Modal
        handleClose={handleClose}
        open={open}
        title={
          get(selectedUsers, 'length') > 1
            ? MESSAGES.modal.deleteUsers.title
            : MESSAGES.modal.deleteUser.title
        }
        body={
          get(selectedUsers, 'length') > 1
            ? MESSAGES.modal.deleteUsers.body
            : MESSAGES.modal.deleteUser.body
        }
        handleSubmit={deleteUser}
      />
      <UserProfileModal
        handleClose={handleCloseEditModal}
        open={openEditModal}
        handleSubmit={deleteUser}
        formData={selectedUser}
        error={error}
        handleCheck={handleCheck}
        saveUserDetails={saveUserDetails}
        handleChange={handleChange}
        loading={loading}
        actionType={actionType}
        selectedUsers={filterSelectedUser()}
        permissionUpdateType={permissionUpdateType}
        setPermissionUpdateType={switchPermissionUpdateType}
        changeBulkPermissionUpdate={changeBulkPermissionUpdate}
        getUserList={getUserList}
      />
    </PageWrapper>
  );
};

UserManagement.propTypes = {
  userManagement: PropTypes.object,
  openNotifier: PropTypes.func.isRequired,
};

UserManagement.defaultProps = {
  userManagement: {},
};

UserManagement.contextTypes = {
  fetch: PropTypes.func.isRequired,
};

export default connect(null, {
  getUsers,
  getGroups,
  changeTab,
  openNotifier,
  getPermissions,
})(withNotifier(withUserManagement(UserManagement)));
