/* eslint-disable import/no-extraneous-dependencies */
import React from 'react';
import PropTypes from 'prop-types';
import { map, sortBy, isEmpty, get, isEqual } from 'lodash';
import {
  withStyles,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button,
  CircularProgress,
  IconButton,
  CloseIcon,
} from '../../../materialLib';
import UserPermissions from '../../../components/UserPermissions';
import UsersCreate from '../../users-create/UsersCreate';
import { COLORS, MESSAGES } from '../../../constants';

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginBottom: 10,
  },
  heading: {
    fontWeight: 'bold',
    margin: 0,
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing.unit,
    top: theme.spacing.unit,
    color: theme.palette.grey[500],
  },
  group: {
    margin: 0,
  },
  radioLabel: {
    color: `${COLORS.NAVY_BLUE} !important`,
  },
});

const UserProfileModal = props => {
  const {
    classes,
    open,
    handleClose,
    handleSubmit,
    error,
    handleChange,
    loading,
    saveUserDetails,
    actionType,
    formData,
    permissionUpdateType,
    changeBulkPermissionUpdate,
    getUserList,
  } = props;
  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="customized-dialog-title"
      open={open}
      maxWidth="lg"
      fullWidth
    >
      <DialogTitle id="customized-dialog-title" onClose={handleClose}>
        <p style={{ marginTop: 10 }}>
          {isEqual(actionType, 'newUser')
            ? MESSAGES.modal.addNewUser.title
            : MESSAGES.modal.users.title}
        </p>
        <IconButton
          color="inherit"
          onClick={handleClose}
          className={classes.closeButton}
          aria-label="Close"
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        {isEqual(actionType, 'newUser') && (
          <UsersCreate handleClose={handleClose} getUserList={getUserList} />
        )}
        {isEqual(actionType, 'existingUser') &&
          isEqual(permissionUpdateType, 'bulkUpdate') && (
            <UserPermissions
              formData={formData}
              error={error}
              handleChange={handleChange}
              handleSubmit={handleSubmit}
              onCancel={handleClose}
              handleCheck={changeBulkPermissionUpdate}
              actionType={actionType}
            />
          )}
      </DialogContent>
      {!isEqual(actionType, 'newUser') && (
        <DialogActions>
          <Button onClick={saveUserDetails} color="primary">
            {loading ? (
              <CircularProgress size={22} style={{ color: COLORS.NAVY_BLUE }} />
            ) : (
              'Ok'
            )}
          </Button>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
        </DialogActions>
      )}
    </Dialog>
  );
};

UserProfileModal.propTypes = {
  open: PropTypes.bool,
  handleClose: PropTypes.func,
  handleSubmit: PropTypes.func,
  formData: PropTypes.object,
  error: PropTypes.object,
  handleChange: PropTypes.func,
  loading: PropTypes.bool,
  saveUserDetails: PropTypes.func,
  actionType: PropTypes.string,
  classes: PropTypes.object,
  permissionUpdateType: PropTypes.string,
  changeBulkPermissionUpdate: PropTypes.func,
  getUserList: PropTypes.func,
};

UserProfileModal.defaultProps = {
  classes: {},
  permissionUpdateType: null,
  handleClose: () => {},
  open: false,
  handleSubmit: () => {},
  formData: {},
  error: {},
  handleChange: () => {},
  loading: () => {},
  saveUserDetails: () => {},
  actionType: null,
  changeBulkPermissionUpdate: () => {},
  getUserList: () => {},
};

export default withStyles(styles)(UserProfileModal);
