/* eslint-disable import/no-duplicates */
/* eslint-disable react/sort-comp */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { some, each, every, chain, omit, filter, get, map } from 'lodash';
import {
  SIGNUP_FIELDS,
  API_URLS,
  MESSAGES,
  DEFAULT_PERMISSIONS,
  ROLES,
} from '../../constants';
import formHelper from '../../lib/formHelper';
import UserProfile from '../../components/UserProfile';
import history from '../../history';
import { openNotifier } from '../../actions/notifier';
import withNotifier from '../../actions/withNotifier';
import withUserManagement from '../../actions/withUserManagement';

class UsersCreate extends React.Component {
  static propTypes = {
    openNotifier: PropTypes.func,
    handleClose: PropTypes.func.isRequired,
    getUserList: PropTypes.func.isRequired,
  };

  static contextTypes = { fetch: PropTypes.func.isRequired };

  static defaultProps = {
    openNotifier: () => { },
  };

  state = {
    formData: {
      firstName: '',
      lastName: '',
      username: '',
      password: '',
      permissions: [],
      roles: ['User'],
    },
    error: {
      firstName: '',
      lastName: '',
      username: '',
      password: '',
    },
    loading: false,
    checked: false,
  };

  componentDidMount() {
    const { formData } = this.state;
    this.setState({
      formData: {
        ...formData,
        permissions: get(this, 'props.userManagement.permissions.length')
          ? get(this, 'props.userManagement.permissions')
          : DEFAULT_PERMISSIONS,
      },
    });
  }

  isValid = () => {
    const { formData, error } = this.state;
    const { firstName, lastName, username, password } = formData;
    if (
      some([username, password, firstName, lastName], formHelper.isInputEmpty)
    ) {
      each(SIGNUP_FIELDS, item => {
        error[item] = formHelper.isInputEmpty(formData[item])
          ? MESSAGES.mandatoryFields
          : '';
      });
    }

    this.setState({ error });

    return every(error, formHelper.isInputEmpty);
  };

  handleSubmit = async e => {
    const { fetch } = this.context;
    let { formData } = this.state;
    const { permissions } = formData;
    let { roles } = formData;
    roles = map(roles, role => ({ role }));
    formData = {
      ...formData,
      permissions: chain(permissions)
        .filter({ checked: true })
        .map(item => omit(item, 'checked'))
        .value(),
      roles,
    };

    e.preventDefault();

    const isValid = await this.isValid();
    if (!isValid || !get(formData, 'roles.length')) {
      await this.props.openNotifier({
        openNotify: true,
        message: MESSAGES.emptyField,
        variant: 'warning',
      });
      return;
    }

    await this.setState({
      loading: true,
    });

    try {
      const response = await fetch(`${API_URLS.signUp}`, {
        method: 'POST',
        body: JSON.stringify(formData),
        headers: {
          'Content-Type': 'application/json',
        },
      });
      const data = await response.json();
      if (response.status === 200) {
        await this.props.openNotifier({
          openNotify: true,
          message: MESSAGES.user.create.success,
          variant: 'success',
        });
      } else {
        await this.props.openNotifier({
          openNotify: true,
          message: data.message,
          variant: 'error',
        });
      }
      await this.props.getUserList();
      await this.props.handleClose();
      await this.setState({ loading: false });
      history.push('/user-management');
    } catch (er) {
      console.error(er);
    }
  };

  handleChange = e => {
    const { formData, error } = this.state;
    const { id, value } = e && e.target;
    formData[id] = value;
    error[id] = formHelper.isInputEmpty(formData[id])
      ? MESSAGES.mandatoryFields
      : '';
    this.setState({ formData, error });
  };

  handleSwitch = name => event => {
    this.setState({ [name]: event.target.checked });
  };

  onCancel = () => {
    this.props.handleClose();
    history.push('/user-management');
  };

  handleCheck = name => event => {
    const { formData } = this.state;
    let { permissions } = formData;
    permissions = filter(permissions, item => item.name !== event.target.value);
    permissions = [
      ...permissions,
      { name: event.target.value, checked: event.target.checked },
    ];
    this.setState({ formData: { ...formData, permissions } });
  };

  handleSelect = event => {
    const { formData } = this.state;
    let { roles } = formData;
    roles = event.target.value;
    if (!event.target.value.length) {
      return;
    }
    this.setState({
      formData: {
        ...formData,
        roles,
      },
    });
  };

  render() {
    const { formData, error, loading, checked } = this.state;
    return (
      <UserProfile
        formData={formData}
        error={error}
        handleChange={this.handleChange}
        handleSubmit={this.handleSubmit}
        type="Create"
        onCancel={this.onCancel}
        createUser
        loading={loading}
        checked={checked}
        handleSwitch={this.handleSwitch}
        handleCheck={this.handleCheck}
        handleSelect={this.handleSelect}
        rolesList={ROLES}
        style={{ width: '60%' }}
      />
    );
  }
}

export default connect(null, {
  openNotifier,
})(withNotifier(withUserManagement(UsersCreate)));
